import * as React from "react";
import ChangedCell from "./ChangedCell";



import './css/TrackedCell.css';
import './css/Margins.css';
import './css/Colors.css';
import './css/Text.css';
import './css/Reset.css';
import './css/Dimensions.css';


export default class TrackedCell extends React.Component {

  constructor(props) {
    super(props); 

    this.state = {
      cellDropdown: false,
      commentDropdown: false,
      comment: ""
    };

    this.toggleCellDropdown = this.toggleCellDropdown.bind(this);
    this.toggleCommentDropdown = this.toggleCommentDropdown.bind(this);
    this.handleCommentChange = this.handleCommentChange.bind(this);
  }
  
  toggleCellDropdown() {
    this.setState({ cellDropdown: !this.state.cellDropdown});
  }

  toggleCommentDropdown() {
    this.setState({ commentDropdown: !this.state.commentDropdown});
  }

  handleCommentChange(e) {
    this.setState({ comment: e.target.value });
  }

  render () {

    let changedCellComponents = [];
    this.props.data.changedInputs.forEach(function(changedCell) {
      changedCellComponents.push(
        <ChangedCell 
          data={changedCell}
            />);
    });

    return (
      <div>
        <div className="tracked-cell-div mr-2">
          <div className="left-column">
            <div className="mr-p5">
              <img src="assets/dropdown.png" alt="comment" width="5" height="9" onClick={this.toggleCellDropdown}/>
            </div>
            <div className="cell-info"> 
              <p className="txt-14 c-b"> {this.props.data.name}</p> 
              <p className="txt-12 c-dg mt-p5"> <b className="txt-b">Old Value:</b> {this.props.data.oldValue}</p> 
              <p className="txt-12 c-dg mt-p5"> <b className="txt-b">New Value:</b> {this.props.data.newValue}</p> 
            </div>
          </div>
          <div className="utilities">
            <div className="comment-div">
              {this.state.comment === "" 
                ? <img src="assets/comment-icon/add-comment.png" alt="comment" width="20" height="20" onClick={this.toggleCommentDropdown}/>
                : <img src="assets/comment-icon/existing-comment.png" alt="comment" width="20" height="20" onClick={this.toggleCommentDropdown}/>
              }
            </div>
            <div className="ml-2">
              <img src="assets/close.png" alt="comment" width="7" height="10" onClick={this.props.untrack}/>
            </div>
          </div>
        </div>
        <div className="ml-2">
          
          {this.state.cellDropdown &&
            <div>
              <hr></hr> 
              <p className="txt-12 c-b">Affected by changes to:</p> 
              {changedCellComponents}
            </div>
          }

          {this.state.commentDropdown &&
            <div className="mt-1">
              <hr></hr>
              <p className="txt-b txt-14">{this.props.data.name} was updated because... </p>
              <form className="comment-dropdown-div">
                <textarea rows = "3" cols = "60" name = "comment" value={this.state.comment} placeholder="complete the sentence" onChange={this.handleCommentChange}></textarea>
                <button type="submit" value="Submit" onClick={this.toggleCommentDropdown}>Add Comment</button>
              </form>
            </div> 
          }  
        </div>
        <hr></hr>
      </div>
    );
  }
}


