/*
Efficiently gets all the worksheet objects with all their names loaded
*/
export async function getSheetsWithNames(context) {
    var sheets = context.workbook.worksheets;
    sheets.load("items/name");
    await context.sync();
    return sheets.items;
}

export async function getSheetsWithVisibility(context) {
    var sheets = context.workbook.worksheets;
    sheets.load("items/visibility");
    await context.sync();
    return sheets.items;
}


export async function makeClique(context, sheetNames, newSheetNames, worksheetPositionType, worksheetVisibility) {
    if (sheetNames.length === 0) {
        return;
    }

    const fileContents = await getFileContents();
    const worksheets = context.workbook.worksheets;

    // First, we insert the new sheets
    worksheets.addFromBase64(
        fileContents,
        sheetNames,
        worksheetPositionType
    );

    await context.sync();

    // Then, we go through and rename all the newly inserted sheets, as well as set their visibility
    // We need to find what their names are, which, for now, we assume will just be renamed + a (2)
    for (let i = 0; i < sheetNames.length; i++) {
        // TODO: handle more complex renamings or inserts
        const insertedName = sheetNames[i] + " (2)";
        const newName = newSheetNames[i];
        const sheet = worksheets.getItem(insertedName);
        sheet.name = newName;
        sheet.visibility = worksheetVisibility;

        // We can queue at most 50 transaction
        if (i % 40 === 0) {
            await context.sync();
        }
    }
    return context.sync();
}

/*
Creates a new sheet with the given name and visibility, 
and returns null if a sheet with that name could not be created
*/
export async function createSheet(context, worksheetName, worksheetVisibility) {
    // copy a sheet
    const activeSheet = context.workbook.worksheets.getActiveWorksheet();
    const copiedSheet = activeSheet.copy(Excel.WorksheetPositionType.end);
    // clear the sheet
    copiedSheet.getUsedRange().clear("all");
    copiedSheet.name = worksheetName;
    copiedSheet.visibility = worksheetVisibility;

    try {
        await context.sync();
    } catch (e) {
        copiedSheet.delete();
        await context.sync();
        return null;
    }

    return copiedSheet;
}

export async function getFileContents() {
    return await getCompressedDocument();
}

// The following example gets the document in Office Open XML ("compressed") format
function getCompressedDocument() {
    return new Promise((resolve, reject) => {
        Office.context.document.getFileAsync(Office.FileType.Compressed, { sliceSize: 65536 /*64 KB*/ }, 
            async function (result) {
                if (result.status == "succeeded") {
                    var myFile = result.value;
                    var sliceCount = myFile.sliceCount;

                    // TODO: make sure we get some error handling going!
                    // so we always close file...
                    const fileContent = await getSlicesAsync(myFile, sliceCount);
                    myFile.closeAsync();

                    resolve(fileContent);
                } else {
                    // reject with error
                    reject(result.error.message);
                }
            }
        );
    });
}
  

async function getSlicesAsync(file, sliceCount) {

    var byteArray = [];
    for (var i = 0; i < sliceCount; i++) {
        const sliceData = await getSliceAsync(file, i);
        byteArray = byteArray.concat(sliceData);
    }
    
    return base64js.fromByteArray(byteArray);
}

function getSliceAsync(file, sliceIndex) {
    return new Promise((resolve, reject) => {
        file.getSliceAsync(sliceIndex, function (sliceResult) {
            if (sliceResult.status == "succeeded") {
                // resolve with the data
                resolve(sliceResult.value.data);
            } else {
                // reject with nothing
                reject(sliceResult.error.message);
            }
        });
    });
}