function chr(codePt) {
    if (codePt > 0xFFFF) { 
        codePt -= 0x10000;
        return String.fromCharCode(0xD800 + (codePt >> 10), 0xDC00 + (codePt & 0x3FF));
    }
    return String.fromCharCode(codePt);
}

export function numToChar (number) {
    var numeric = (number - 1) % 26;
    var letter = chr(65 + numeric);
    var number2 = parseInt((number - 1) / 26);
    if (number2 > 0) {
        return numToChar(number2) + letter;
    } else {
        return letter;
    }
}


var toColumnNum = function(val) {
    var base = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ', i, j, result = 0;
  
    for (i = 0, j = val.length - 1; i < val.length; i += 1, j -= 1) {
      result += Math.pow(base.length, j) * (base.indexOf(val[i]) + 1);
    }
  
    return result;
  };
//https://stackoverflow.com/questions/9905533/convert-excel-column-alphabet-e-g-aa-to-number-e-g-25

/*
    This function wraps a 2d array of cell values. For cells that are not in the
    2d array, it pretends that they are just empty. This makes cell-based comparison 
    very easy for different sized ranges.
*/
export class ValueWrapper {
    constructor(values) {
        this.values = values;
    }

    /*
        Check if the row is defind, then check if the cell is defined, and return it if so.
        Otherwise, return an empty cell value.
    */
    getCell(row, col) {
        return this.values[row] ? (this.values[row][col] ? this.values[row][col] : "") : "";
    }
}

// find all of the changes between two 2D array representations of a sheets
export function simpleDiff2D(oldValues, newValues, sheetName) {

    const maxNumRows = Math.max(oldValues.length, newValues.length);
    const maxNumCols = Math.max(oldValues[0] ? oldValues[0].length : 0, newValues[0] ? newValues[0].length : 0);

    const oldValueWrapper = new ValueWrapper(oldValues);
    const newValueWrapper = new ValueWrapper(newValues);


    const changes = [];
    for (let i = 0; i < maxNumRows; i++) {
        for (let j = 0; j < maxNumCols; j++) {
            const oldValue = oldValueWrapper.getCell(i, j);
            const newValue = newValueWrapper.getCell(i, j);

            const columnName = numToChar(j + 1);
            const excelRow = i + 1;
            const address = columnName + excelRow;

            if (oldValue !== newValue) {
                changes.push({
                    sheetName: sheetName,
                    address: address,
                    oldValue: oldValue, 
                    newValue: newValue
                });
            }

        }
    }

    return changes;
}

function replaceSheetRefs(formulas, oldSheetName, newSheetName) {
    for (let i = 0; i < formulas.length; i++) {
        for (let j = 0; j < formulas[i].length; j++) {
            let formula = formulas[i][j];
            if (formula[0] === "=") {
                // Taken from https://stackoverflow.com/questions/171480/regex-grabbing-values-between-quotation-marks
                formula = formula.replaceAll(/(['])(?:(?=(\\?))\2.)*?\1/g, (quoted) => {
                    if (quoted.startsWith("\'s&")) {
                        return quoted.slice(3, quoted.length - 1);
                    }
                    return quoted;
                });
            }
            formulas[i][j] = formula;
        }
    }
    return formulas;
}


export async function getFormulas(context, sheet) {
    var usedRange = sheet.getUsedRangeOrNullObject(true);
    // Have to load and then sync to run the command
    usedRange.load("formulas");
    usedRange.load("address");
    usedRange.load("isNullObject");
    await context.sync();
    
    if (usedRange.isNullObject) {
        return [[]];
    }

    const addrParts = usedRange.address.split(":");

    if (addrParts[0] === "A1") {
        return usedRange.formulas;
    }

    // Redefine the box to include the 
    const bottomRight = addrParts.length === 1 ? addrParts[0] : addrParts[1];

    var usedRangeWithA1 = sheet.getRange(`A1:${bottomRight}`);
    usedRangeWithA1.load("formulas");
    await context.sync();

    return usedRangeWithA1.formulas;
}

async function matchedSheets(context) {
    let sheets = await getSheetsWithVisibility(context);
    sheets.forEach(sheet => sheet.load("name"));
    await context.sync();

    // Now, we get the visible sheets, as they are the current workbook
    const current = sheets.filter(sheet => sheet.visibility === Excel.SheetVisibility.visible);

    // The hidden sheets (that aren's the saga sheet) are the last check in
    const previous = sheets.filter(sheet => { return sheet.visibility !== Excel.SheetVisibility.visible && sheet.name !== "saga";});

    let matches = [];
    for (let i = 0; i < previous.length; i++) {
        const oldSheet = previous[i];
        const newSheet = current.find(sheet => "s&" + sheet.name === oldSheet.name);
        matches.push([oldSheet, newSheet]);
    }
    return matches;
} 

async function matchedValueWrapperMap(context) {
    const matched = await matchedSheets(context);

    let wrapperMap = {};
    for (let i = 0; i < matched.length; i++) {
        const [ oldSheet, newSheet ] = matched[i];
        const sheetName = newSheet.name;

        const oldFormulas = replaceSheetRefs(await getFormulas(context, oldSheet));
        const newFormulas = await getFormulas(context, newSheet);

        const oldWrapper = new ValueWrapper(oldFormulas);
        const newWrapper = new ValueWrapper(newFormulas);

        wrapperMap[sheetName] = [oldWrapper, newWrapper];
    }

    return wrapperMap;
}


// Builds a diff function from the last saved version to the current version
export async function diff(context) {
    const matched = await matchedSheets(context);

    let allChanges = [];
    for (let i = 0; i < matched.length; i++) {
        const [oldSheet, newSheet] = matched[i];
        const sheetName = newSheet.name;

        // Then, we can calculate the differences between them
        const oldFormulas = replaceSheetRefs(await getFormulas(context, oldSheet));
        const newFormulas = await getFormulas(context, newSheet);

        // Now, we can detect differences between them
        const changes = simpleDiff2D(oldFormulas, newFormulas, sheetName);

        allChanges.push({
            "sheetName": sheetName,
            "changes": changes
        });
    }

    return allChanges;
}

