function getColRow(address) {
    // In case it has a sheet name
    if (address.includes("!")) {
        address = address.split("!")[1];
    }
    const col = address.match(/[a-zA-Z]+/g)[0];
    let row = address.match(/[0-9]+/g)[0];
    return [col, row];
}

export async function trackedOutputChanges(context, bindings) {
    const wrapperMap = await matchedValueWrapperMap(context);

    let trackedOutputs = [];
    for (let i = 0; i < bindings.length; i++) {
        const binding = bindings[i];
        const { name, sheetName, address } = JSON.parse(binding.bindingName);
        const [ oldWrapper, newWrapper ] = wrapperMap[sheetName];

        const [col, row] = getColRow(address);
        const colNum = toColumnNum(col) - 1;
        const rowNum = parseInt(row) - 1;

        const oldValue = oldWrapper.getCell(rowNum, colNum);
        const newValue = newWrapper.getCell(rowNum, colNum);

        const possibleChangedInputs = binding.edits;
        const changedInputs = [];
        possibleChangedInputs.forEach((inputAddress) => {
            // TODO: fix up w/ sheet
            const [iSheetName, iAddress] = inputAddress.split("!");
            const [iCol, iRow] = getColRow(iAddress);
            const iColNum = toColumnNum(iCol) - 1;
            const iRowNum = parseInt(iRow) - 1;

            const [ iOldWrapper, iNewWrapper ] = wrapperMap[iSheetName];

            const oldInputValue = iOldWrapper.getCell(iRowNum, iColNum);
            const newInputValue = iNewWrapper.getCell(iRowNum, iColNum);

            if (oldInputValue != newInputValue) {
                changedInputs.push({
                    "sheetName": iSheetName,
                    "address": iAddress,
                    "oldValue": oldInputValue,
                    "newValue": newInputValue
                });
            }
        })
        trackedOutputs.push({
            "name": name,
            "sheetName": sheetName,
            "address": address,
            "oldValue": oldValue,
            "newValue": newValue,
            "changedInputs": changedInputs
        })
    }

    return trackedOutputs;
}